<template>
  <div class="app-container member-new">

    <el-card class="box-card">
      <el-form ref="form" :model="form" :rules="formRules" label-width="150px" :label-position="labelPosition">
        <el-form-item label="会员照">
          <el-button type="primary" @click="emitFile">上传<input ref="file" type="file" accept="image/*" style="opacity: 0; position: fixed; top: -1000px;" @change="upload"></el-button>
          <el-button type="primary" v-show="!isMobile" @click="dialogCamera">拍照</el-button>
          <div class="avatar-preview" v-if="!!form.photo">
            <div class="avatar-wrap">
              <img :src="form.photo" class="avatar" @click="dialogImg">
            </div>
            <i class="el-icon-delete avatar-uploader-icon" @click="clearAvatar"></i>
          </div>
          <el-dialog v-model="dialogVisible" size="tiny">
            <img width="100%" :src="form.photo" alt="">
          </el-dialog>
          <el-dialog v-model="isShowCamera" size="full">
            <camera :isShowCamera="isShowCamera" @getCamera="getCamera"></camera>
          </el-dialog>
        </el-form-item>
        <el-form-item label="姓名" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-radio-group v-model="form.sex">
            <el-radio :label="1">男</el-radio>
            <el-radio :label="2">女</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="电话" prop="mobile">
          <el-input v-model="form.mobile"></el-input>
        </el-form-item>
        <el-form-item label="所属场地" prop="store_id">
          <el-radio-group v-model="form.store_id">
            <el-radio :label="2">龙珠店</el-radio>
            <el-radio :label="1">海德店</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="会员卡类型" prop="card_type_name">
          <div v-if="!!isMobile" class="el-form-item__content">
            <div class="el-input">
              <div class="el-input__inner" style="line-height: 30px; color: #8391a5;" @click="showCardTypePicker">{{ form.card_type_name.length !== 0 ? form.card_type_name : '会员卡类型'}}</div>
            </div>
          </div>
          <div v-if="!!isMobile" v-show="cardTypePicker" style="position: fixed; bottom: 0; left: 0; right: 0; background: #fff; z-index: 9999">
            <div style="height: 32px; line-height: 32px; text-align: center; overflow: hidden; border-bottom: 1px solid #eaeaea; color: #26a2ff">
              <div style="float: left; width: 50%" @click="hidePicker">取消</div>
              <div style="float: right; width: 50%" @click="selectedCardType">确定</div>
            </div>
            <mt-picker :slots="cardTypeOptionsM" @change="onCardTypeChange" :visible-item-count="5"></mt-picker>
          </div>
          <el-cascader v-else :options="cardTypeOptions" v-model="form.card_type_name">
          </el-cascader>
        </el-form-item>
        <el-form-item v-show="isShowCardCishu" label="卡片次数" prop="card_use_num">
          <el-input v-model="form.card_use_num" type="number"></el-input>
        </el-form-item>
        <el-form-item label="赠送时间（月）" prop="give_time">
          <el-input v-model="form.give_time" type="number" @change="giveTimeChange"></el-input>
        </el-form-item>
        <el-form-item label="卡片有效起始日期">
          <el-date-picker v-model="form.card_start_time" type="date" placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="卡片有效结束日期">
          <el-date-picker v-model="form.card_end_time" type="date" placeholder="选择日期" :readonly="pickerEndDay">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="绑定会员卡物理ID">
          <el-tooltip class="item" effect="dark" :content="(isCanReadCard ? '点击按钮读卡' : '放置卡片读卡')" placement="top">
            <el-input v-model="form.card_id" placeholder="读卡器读取">
              <el-button v-if="isCanReadCard" slot="prepend" @click="readCard">
                <span class="read-card">
                  <icon-svg icon-class="readcard" />
                </span>
              </el-button>
              <el-button v-if="isCanReadCard" slot="append" icon="delete" @click="clearCardID">
              </el-button>
            </el-input>
          </el-tooltip>
        </el-form-item>
        <el-form-item label="微信">
          <el-input v-model="form.wechat_code"></el-input>
        </el-form-item>
        <!-- <el-form-item label="昵称">
                                  <el-input v-model="form.user_nickname"></el-input>
                                </el-form-item> -->
        <el-form-item label="生日">
          <el-date-picker v-model="form.birthday" type="date" @change="datePickerChange" placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="身份证号">
          <el-input v-model="form.id_card"></el-input>
        </el-form-item>
        <!--<el-form-item label="身高">
                                  <el-input v-model="form.shengao"></el-input>
                                </el-form-item>
                                <el-form-item label="体重">
                                  <el-input v-model="form.tizhong"></el-input>
                                </el-form-item>-->
        <el-form-item label="家庭地址">
          <city :addressValue.sync="form.home_address"></city>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="home_address_detail"></el-input>
        </el-form-item>
        <el-form-item label="工作地址">
          <city :addressValue.sync="form.work_address"></city>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="work_address_detail"></el-input>
        </el-form-item>
        <el-form-item label="来源">
          <el-input v-model="form.referer"></el-input>
        </el-form-item>
        <el-form-item label="销售">
          <el-input v-model="form.sale_staff"></el-input>
        </el-form-item>
        <el-form-item label="教练">
          <el-input v-model="form.coach_staff"></el-input>
        </el-form-item>
        <el-form-item label="备注">
          <el-input type="textarea" v-model="form.remark"></el-input>
        </el-form-item>
      </el-form>
    </el-card>

    <div class="save">
      <el-button type="primary" @click="submitForm('form')">保存</el-button>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import { upload } from '@/api/member';
import { isMobile, nIndexOf, imgBeforeUpload } from '@/utils';
import { getCitys } from '@/api';
import { add, edit } from '@/api/member';
import router from '../../router';
import camera from '@/components/camera/index';
import city from '@/components/city/index';

export default {
  components: {
    camera,
    city
  },
  data() {
    // 验证手机号码
    const validatePhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('电话不能为空'));
      }
      if ((/^1[34578]\d{9}$/.test(value))) {
        return callback();
      }
      callback(new Error('请输入正确的手机号码，如：13600002388'));
    }

    // 验证不为空
    const validateEmpty = (rule, value, callback) => {
      if (typeof value !== 'object' ? !value : !value.length) {
        return callback(new Error(rule.errMsg));
      }
      callback();
    }

    // 验证次数
    const validateCishu = (rule, value, callback) => {
      if (value === '') {
        return callback();
      }
      if (value < 0 || value > 1000) {
        return callback(new Error('次数必须在0-1000之间'));
      }
      callback();
    }

    // 验证赠送月数
    const validateGiveTime = (rule, value, callback) => {
      if (value < 0) {
        return callback(new Error('赠送时间不能为负'));
      }
      callback();
    }

    return {
      // 是否显示照相机
      isShowCamera: false,
      form: {
        photo: '',
        name: '',
        sex: '',
        mobile: '',
        store_id: '',
        card_id: '',
        wechat_code: '',
        // user_nickname: '',
        birthday: '',
        id_card: '',
        // shengao: '',
        // tizhong: '',
        home_address: '',
        work_address: '',
        referer: '',
        sale_staff: '',
        coach_staff: '',
        remark: '',
        // 卡片起始日期
        card_start_time: '',
        // 卡片结束日期
        card_end_time: '',
        // 会员卡类型
        card_type: 0,
        // 会员卡类型名称
        card_type_name: [],
        // 卡片次数
        card_use_num: 1,
        // 会员卡赠送时间
        give_time: 0
      },
      // 家庭地址详情
      home_address_detail: '',
      // 工作地址详情
      work_address_detail: '',

      // 电脑端卡片数据配置
      cardTypeOptions: [],
      // 手机端卡片数据
      cardTypeDataM: [],
      // 手机端卡片数据配置      
      cardTypeOptionsM: [],
      // 卡片有效月数
      cardMonth: '',
      // 是否可以选择结束时间
      pickerEndDay: true,
      // 显示出来的到期日期
      show_valid_card_time: '',
      // 是否显示输入卡片次数
      isShowCardCishu: false,

      formRules: {
        name: [
          { validator: validateEmpty, errMsg: '请输入姓名', trigger: 'blur' }
        ],
        mobile: [
          { validator: validatePhone, trigger: 'blur' }
        ],
        sex: [
          { validator: validateEmpty, errMsg: '请选择性别', trigger: 'change' }
        ],
        store_id: [
          { validator: validateEmpty, errMsg: '请选择门店', trigger: 'change' }
        ],
        card_use_num: [
          { validator: validateCishu, trigger: 'blur' }
        ],
        give_time: [
          { validator: validateGiveTime, trigger: 'blur' }
        ]
      },
      // 显示上传的图片
      dialogVisible: false,
      cardType: '',
      cardTypePicker: false
    }
  },
  computed: {
    ...mapState([
      'winsize'
    ]),
    ...mapGetters([
      'isCanReadCard',
      'cardID',
      'memberInfo',
      'isGettingInfo'
    ]),
    labelPosition() {
      return (this.winsize === 'xs') ? 'top' : 'right';
    },
    isMobile() {
      return isMobile();
    },
    startDate() {
      // 获取当前年份
      const nowYear = new Date().getFullYear();
      console.log(new Date((nowYear - 50) + '-1-1'));
      return new Date((nowYear - 50) + '-1-1');
    }
  },
  created() {
    this.getCardType();

    if (this.$route.name === '修改会员资料') {
      console.log('当前为编辑模式');
      const params = {
        mobile: '',
        id: this.$route.params.id
      }
      console.log(this.$route);
      this.getMemberInfo(params);
      console.log(this.memberInfo);

      if (this.memberInfo) {
        this.setMemberInfo();
      }
    }

  },
  watch: {
    memberInfo() {
      console.log('拉去完会员信息');
      console.log(this.memberInfo);
      this.setMemberInfo();
    },
    readCardID() {
      if (this.readCardID) {
        this.form.cardID = this.readCardID;
      }
    },
    'form.card_type_name': function() {
      console.log('卡片类型改变了');
      var card_type_name = this.form.card_type_name;
      if (this.isMobile && this.form.card_type_name.length !== 0) {
        card_type_name = this.form.card_type_name.split(' / ');
      }
      console.log(card_type_name, '------1');

      // if (card_type_name[0] === '次卡') {
      //   console.log('次卡');
      //   this.isShowCardCishu = true;
      //   return;
      // }

      this.form.card_type = !(card_type_name[0] === '次卡') ? 1 : 2;
      this.pickerEndDay = !(card_type_name[0] === '次卡');
      this.isShowCardCishu = (card_type_name[0] === '次卡');
      for (let i = 0; i < this.cardTypeOptions.length; i++) {
        if (card_type_name[1]) {
          for (let j = 0; j < this.cardTypeOptions[i].children.length; j++) {
            if (this.cardTypeOptions[i].children[j].label === card_type_name[1]) {
              this.cardMonth = this.cardTypeOptions[i].children[j].month;
              this.form.card_start_time = this.form.card_start_time || Date.now();
              this.setCardTime();
              return;
            }
          }
        } else {
          if (this.cardTypeOptions[i].label === card_type_name[0]) {
            this.cardMonth = this.cardTypeOptions[i].month;
            this.form.card_start_time = this.form.card_start_time || Date.now();
            this.setCardTime();
          }
        }
      }
      console.log('------2');
    },
    'form.card_start_time': function() {
      if (!this.form.card_start_time) {
        this.form.card_type_name = [];
        this.form.card_end_time = '';
        this.show_valid_card_time = '';
        this.cardMonth = '';
        return;
      }
      this.setCardTime();
    },
  },
  methods: {
    giveTimeChange() {
      console.log('00000');
      if (this.form.give_time < 0) {
        return;
      }
      this.setCardTime()
    },
    ...mapMutations({
      setMemberInfo1: 'member/setInfo'
    }),
    // 修改页面，将获取到的会员信息绑定到页面上
    setMemberInfo() {
      console.log('设置会员数据', this.memberInfo.card_type_name, JSON.parse(JSON.stringify(this.memberInfo)));
      let form = JSON.parse(JSON.stringify(this.memberInfo));
      let cardTypeNameArr = form.card_type_name.split(' / ');
      let month;
      console.log(this.cardTypeOptions);
      for (let i = 0; i < this.cardTypeOptions.length; i++) {
        if (cardTypeNameArr[1] && this.cardTypeOptions[i].children) {
          for (let j = 0; j < this.cardTypeOptions[i].children.length; j++) {
            if (this.cardTypeOptions[i].children[j].label === cardTypeNameArr[1]) {
              month = this.cardTypeOptions[i].children[j].month;
              break;
            }
          }
        } else {
          if (this.cardTypeOptions[i].label === cardTypeNameArr[0]) {
            month = this.cardTypeOptions[i].month;
          }
        }
      }

      console.log('当前的会员卡月份', month);

      if (!this.isMobile) {
        console.log(this.form.card_type_name);
        form.card_type_name = form.card_type_name.split(' / ');
      }

      let homeDetailIndex = nIndexOf(form.home_address, ' ', 2);
      let workDetailIndex = nIndexOf(form.work_address, ' ', 2);
      this.home_address_detail = form.home_address.substring(homeDetailIndex + 1);
      this.work_address_detail = form.work_address.substring(workDetailIndex + 1);
      form.home_address = form.home_address.substring(0, homeDetailIndex);
      form.work_address = form.work_address.substring(0, workDetailIndex);

      form.card_start_time = form.card_start_time * 1000 || '';
      form.card_end_time = form.card_end_time * 1000 || '';
      form.birthday = form.birthday * 1000 || '';

      this.form = form;

      // 根据卡片结束与起始来计算赠送的月数
      let start = new Date(form.card_start_time),
        startYear = start.getFullYear(),
        startMonth = start.getMonth();
      let end = new Date(form.card_end_time),
        endYear = end.getFullYear(),
        endMonth = end.getMonth();
      this.form.give_time = (endYear - startYear) * 12 + (endMonth - startMonth) - month;
      // this.form.home_address = this.memberInfo.home_address;
    },
    // 设置卡片有效期
    setCardTime() {
      let now = new Date(this.form.card_start_time);
      let year = now.getFullYear();
      let month = now.getMonth();
      let day = now.getDate();
      let valid_card_time = new Date(year, month + 1 * this.cardMonth + 1 * (this.form.give_time || 0), day);
      this.form.card_end_time = valid_card_time.getTime();
      this.show_valid_card_time = valid_card_time.getFullYear() + '-' + (valid_card_time.getMonth() + 1) + '-' + valid_card_time.getDate();
    },
    ...mapActions({
      actionReadCard: 'actionReadCard',
      getMemberInfo: 'member/getInfo'
    }),
    clearCardID() {
      this.form.card_id = undefined;
    },
    readCard() {
      if (this.isCanReadCard) {
        this.actionReadCard();
        this.form.card_id = this.cardID;
        console.log('读卡', this.form.card_id, this.cardID);
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.onSubmit();
        } else {
          this.$message.error('提交失败，请检查表单！');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    onSubmit() {
      console.log('submit!', this.form);
      const params = JSON.parse(JSON.stringify(this.form));
      params.birthday = this.form.birthday ? new Date(this.form.birthday).getTime() / 1000 : '';
      if (!this.isMobile) {
        params.card_type_name = this.form.card_type_name[0] ? this.form.card_type_name[0] + (this.form.card_type_name[1] ? ' / ' + this.form.card_type_name[1] : '') : '';
      } else {
        console.log(this.form.card_type_name);
        params.card_type_name = this.form.card_type_name.length ? params.card_type_name : '';
      }
      // params.card_id = params.card_id || '尚未绑定卡片';
      params.home_address = this.form.home_address + ' ' + this.home_address_detail;
      params.work_address = this.form.work_address + ' ' + this.work_address_detail;
      params.card_start_time = this.form.card_start_time ? Math.round(this.form.card_start_time / 1000) : '';
      params.card_end_time = this.form.card_end_time ? Math.round(this.form.card_end_time / 1000) : '';
      console.log('submit!', params);
      if (this.$route.name === '修改会员资料') {
        edit(params).then(response => {
          this.$message.success('修改会员资料成功');
          console.log(params, response.data);
          this.setMemberInfo1(params);
          router.push({ name: '会员资料', params: { id: params.id } });
        });
      } else {
        add(params).then(response => {
          this.$message.success('添加会员成功');
          console.log(params, response.data);
          router.push({ name: '会员列表' });
        });
      }
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }
      return isJPG && isLt2M;
    },
    emitFile() {
      this.$refs.file.click();
    },
    upload(e) {
      // 参考：http://www.cnblogs.com/cjh1111/p/7017295.html
      let file = e.target.files[0];
      if (!file) {
        return;
      }
      imgBeforeUpload(file).then(rst => {
        upload(rst.formData).then(response => {
          this.form.photo = 'http://' + response.data.pic_address;
        });
      }).catch(err => {
          this.$message.error(err.toString());
        });
    },
    // 获取拍照后的图片地址
    getCamera(imgSrc) {
      this.form.photo = imgSrc;
      this.isShowCamera = false;
    },
    clearAvatar() {
      this.form.photo = '';
    },
    datePickerChange(value) {
      console.log(value, this.form.birthday);
    },
    dialogImg() {
      this.dialogVisible = true;
    },
    getCardType() {
      var cardTypeOptions = [
        {
          label: '金卡',
          value: '金卡',
          children: [
            {
              label: '一年卡',
              value: '一年卡',
              price: '2688',
              month: '12'
            },
            {
              label: '买一年送一年卡',
              value: '买一年送一年卡',
              price: '2688',
              month: '24'
            }
          ]
        }, {
          label: '铂金卡',
          value: '铂金卡',
          children: [
            {
              label: '两年卡',
              value: '两年卡',
              price: '2688',
              month: '24'
            },
            {
              label: '双人两年卡',
              value: '双人两年卡',
              price: '2688',
              month: '24'
            },
            {
              label: '买两年送两年卡',
              value: '买两年送两年卡',
              price: '2688',
              month: '48'
            }
          ]
        }, {
          label: '董事卡',
          value: '董事卡',
          children: [
            {
              label: '三年卡',
              value: '三年卡',
              price: '2688',
              month: '36'
            }
          ]
        }, {
          label: '至尊卡',
          value: '至尊卡',
          children: [
            {
              label: '双人四年卡',
              value: '双人四年卡',
              price: '2688',
              month: '48'
            },
            {
              label: '五年卡带一人',
              value: '五年卡带一人',
              price: '2688',
              month: '60'
            },
          ]
        }, {
          label: '尊爵卡',
          value: '尊爵卡',
          children: [
            {
              label: '四人四年卡',
              value: '四人四年卡',
              price: '2688',
              month: '48'
            },
            {
              label: '三人五年卡',
              value: '三人五年卡',
              price: '2688',
              month: '60'
            },
          ]
        }, {
          label: '月卡',
          value: '月卡',
          children: [
            {
              label: '一个月',
              value: '一个月',
              price: '2688',
              month: '1'
            },
            {
              label: '两个月',
              value: '两个月',
              price: '2688',
              month: '2'
            },
          ]
        }, {
          label: '季卡',
          value: '季卡',
          price: '1788',
          month: '3'
        }, {
          label: '半年卡',
          value: '半年卡',
          price: '1788',
          month: '6'
        }, {
          label: '次卡',
          value: '次卡',
          price: '1788',
          month: '12'
        }
      ];
      this.cardTypeOptions = cardTypeOptions;
      this.cardTypeOptionsM = this.formatCardType2(cardTypeOptions);
    },
    formatCardType1(cardTypeOptions) {

    },
    formatCardType2(cardTypeOptions) {
      const cardType = {};

      for (let i = 0; i < cardTypeOptions.length; i++) {
        var key = cardTypeOptions[i].label;
        var children = [];
        if (cardTypeOptions[i].children) {
          for (let j = 0; j < cardTypeOptions[i].children.length; j++) {
            children[j] = cardTypeOptions[i].children[j].label;
          }
        }
        cardType[key] = children;
      }

      this.cardTypeDataM = cardType;
      console.log(cardType);

      var options = [
        {
          flex: 1,
          values: Object.keys(cardType),
          className: 'slot1',
          textAlign: 'center'
        }, {
          divider: true,
          content: '-',
          className: 'slot2'
        }, {
          flex: 1,
          values: [''],
          className: 'slot3',
          textAlign: 'center'
        }
      ];

      return options;
    },
    // 移动端点击显示级联选择器
    showCardTypePicker() {
      this.cardTypePicker = true;
    },
    // 卡片类型改变触发事件
    onCardTypeChange(picker, values) {
      console.log('--------------', this.cardTypeDataM);
      picker.setSlotValues(1, this.cardTypeDataM[values[0]]);
      this.cardType = values[0] + (values[1] ? ' / ' + values[1] : '');
      console.log(values);
    },
    // 点击确定
    selectedCardType() {
      this.cardTypePicker = false;
      this.form.card_type_name = this.cardType;
    },
    hidePicker() {
      this.cardTypePicker = false;
    },
    // 显示照相机
    dialogCamera() {
      this.isShowCamera = true;
    }
  }
}
</script>

<style>
.member-new .el-form {
  width: 100%;
  max-width: 600px;
}

.member-new .save {
  margin: 20px;
  text-align: center;
}

.avatar-preview {
  position: relative;
  margin-top: 20px;
}

.avatar-preview .avatar-wrap {
  width: 100px;
  height: 100px;
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.avatar-preview .avatar-wrap:hover {
  border-color: #20a0ff;
}

.avatar-uploader-icon {
  position: absolute;
  top: -10px;
  left: 90px;
  font-size: 20px;
  color: #8c939d;
  background: rgba(255, 255, 255, .2);
  text-align: center;
  cursor: pointer;
}

.avatar-uploader-icon:hover {
  color: #ff4949;
}

.avatar {
  width: 100px;
  height: 100px;
  display: block;
}

.read-card .svg-icon {
  font-size: 20px;
}
</style>



