<template>
    <div class="">
        <div v-if="!!isMobile" class="el-form-item__content">
            <div class="el-input">
                <div class="el-input__inner" style="line-height: 30px; color: #8391a5;" @click="showPicker">{{ address || '家庭住址'}}</div>
            </div>
        </div>
        <div v-if="!!isMobile" v-show="mtPicker" style="position: fixed; bottom: 0; left: 0; right: 0; background: #fff; z-index: 9999">
            <div style="height: 32px; line-height: 32px; text-align: center; overflow: hidden; border-bottom: 1px solid #eaeaea; color: #26a2ff">
                <div style="float: left; width: 50%" @click="hidePicker">取消</div>
                <div style="float: right; width: 50%" @click="ok">确定</div>
            </div>
            <mt-picker :slots="addressSlots" @change="onAddressChange" :visible-item-count="5"></mt-picker>
        </div>
        <el-cascader v-else :options="options" v-model="address">
        </el-cascader>
    </div>
</template>

<script>
import { isMobile } from '@/utils';
import citys from '@/assets/citys';

export default {
    props: ['addressValue'],
    data() {
        return {
            mtPicker: false,
            options: null,
            selected: null,
            addressSlots: null,
            addressShengShi: null,
            addressShiQu: null,
            address: null,
            value: null
        }
    },
    computed: {
        isMobile() {
            return isMobile();
        }
    },
    watch: {
        addressValue() {
            this.address = this.formatGetValue(this.addressValue);
        },
        address() {
            this.addressChange();
        }
    },
    methods: {
        showPicker() {
            this.mtPicker = true;
        },
        hidePicker() {
            this.mtPicker = false;
        },
        onAddressChange(picker, values) {
            picker.setSlotValues(1, this.addressShengShi[values[0]]);
            picker.setSlotValues(2, (this.addressShiQu[values[1]] || ''));
            this.selected = values[0] + ' / ' + values[1] + (values[2] ? ' / ' + values[2] : '');
            console.log(values, this.selected);
        },
        ok() {
            console.log('点击ok', this.selected);
            this.mtPicker = false;
            this.address = this.selected;
            console.log(this.address);
        },
        // 地址改变
        addressChange() {
            console.log('地址发生改变');
            if (!this.isMobile) {
                this.value = this.address.length === 0 ? '' : this.address[0] + ' ' + this.address[1] + ' ' + this.address[2];
            } else {
                const address = typeof this.address === 'string' ? this.address.split(' / ') : this.address;
                this.value = address.length === 0 ? '' : address[0] + ' ' + address[1] + ' ' + address[2];
            }
            console.log(this.value);
            // 子父组件数据双向绑定
            this.$emit('update:addressValue', this.value);
            console.log(this.addressValue);
        },
        // 将城市数据格式化为element-ui的级联选择器数据格式
        formatCitysToCascader() {
            let options = [];
            let shengIndex = -1;
            let shiIndex = -1;
            let quIndex = -1;

            for (let [key, value] of Object.entries(citys)) {
                // 首先判断是不是省
                if (key % 10000 === 0) {
                    shengIndex++;
                    shiIndex = -1;
                    options[shengIndex] = {
                        value,
                        label: value,
                        children: []
                    }
                    continue;
                }

                // 如果是市
                if (key % 10000 !== 0 && key % 100 === 0) {
                    shiIndex++;
                    quIndex = -1;
                    options[shengIndex].children[shiIndex] = {
                        value,
                        label: value,
                        children: []
                    }
                    continue;
                }

                // 如果没有市
                if (shiIndex === -1) {
                    shiIndex = 0;
                    quIndex = -1;
                    options[shengIndex].children[shiIndex] = {
                        value: options[shengIndex].value,
                        label: options[shengIndex].value,
                        children: []
                    }
                }

                quIndex++;
                options[shengIndex].children[shiIndex].children[quIndex] = {
                    value,
                    label: value
                }
            }

            console.log(options);
            this.options = options;
        },
        // 手机端城市数据格式
        formatCitysToPicker() {

            let addressShengShi = [];
            let addressShiQu = [];
            let sheng = null;
            let shi = null;

            for (let [key, value] of Object.entries(citys)) {
                // 首先判断是不是省
                if (key % 10000 === 0) {
                    sheng = value;
                    shi = null;
                    addressShengShi[sheng] = [];
                    continue;
                }

                // 如果是市
                if (key % 10000 !== 0 && key % 100 === 0) {
                    shi = value;
                    addressShengShi[sheng].push(shi);
                    addressShiQu[shi] = [];
                    continue;
                }

                // 如果没有市
                if (!shi) {
                    shi = sheng;
                    addressShengShi[sheng].push(shi);
                    addressShiQu[shi] = [];
                }

                addressShiQu[shi].push(value);
            }


            this.addressShengShi = addressShengShi;
            this.addressShiQu = addressShiQu;
            this.addressSlots = [
                {
                    flex: 1,
                    values: Object.keys(addressShengShi),
                    className: 'slot1',
                    textAlign: 'center'
                }, {
                    divider: true,
                    content: '-',
                    className: 'slot2'
                }, {
                    flex: 1,
                    values: ['北京'],
                    className: 'slot3',
                    textAlign: 'center'
                }, {
                    divider: true,
                    content: '-',
                    className: 'slot4'
                }, {
                    flex: 1,
                    values: ['北京'],
                    className: 'slot5',
                    textAlign: 'center'
                }
            ]
        },
        // 格式化从父组件传过来的值
        formatGetValue(addressValue) {
            let address;
            let addressValueArr = addressValue.split(' ');
            if (!this.isMobile) {
                address = [];
                address[0] = addressValueArr[0];
                address[1] = addressValueArr[1];
                address[2] = addressValueArr[2];
            } else {
                address = addressValueArr[0] + ' / ' + addressValueArr[1] + ' / ' + addressValueArr[2];
            }
            return address;
        }
    },
    created() {
        console.log('this.addressValue', this.addressValue);
        this.formatCitysToCascader();
        if (this.addressValue) {
            this.address = this.formatGetValue(this.addressValue);
        }
        this.formatCitysToPicker();
    },
    mounted() {
        console.log(this.addressSlots);
    }
}
</script>


