<template>
    <div class="camera">
        <!-- FLASH调用 -->
        <div v-if="doVerificationBrowser()" ref="FalshDiv" style="text-align: center">
            <object v-show="!isPhoto" style="z-index: 100" id="My_Cam" align="middle" classid="clsid:d27cdb6e-ae6d-11cf-96b8-444553540000" codebase="http://fpdownload.macromedia.com/pub/shockwave/cabs/flash/swflash.cab#version=8,0,0,0" height="450" viewastext="在线拍照" width="600">
                <param name="allowScriptAccess" value="sameDomain" />
                <param name="movie" :value="MyCameraSwf" />
                <param name="quality" value="high" />
                <param name="bgcolor" value="#000000" />
                <param name="wmode" value="transparent" />
                <embed style="z-index: 100" align="middle" allowscriptaccess="sameDomain" bgcolor="#ffffff" height="450" name="My_Cam" pluginspage="http://www.macromedia.com/go/getflashplayer" quality="high" wmode="transparent" :src="MyCameraSwf" type="application/x-shockwave-flash" width="600"></embed>
            </object>
            <img v-show="isPhoto" width="600" height="450" :src="'data:image/png;base64,' + base64Data" />
        </div>
        <!-- HTML5调用 -->
        <div v-else ref="HTML5Div" style="text-align:center;">
            <video v-show="!isPhoto" ref="cameraVideo" width="600" autoplay></video>
            <canvas v-show="isPhoto" ref="cameraCanvas" width="600" height="450"></canvas>
        </div>
        <div style="text-align: center; margin-top: 20px;">
            <el-button v-show="!isPhoto" type="primary" @click="photo()">拍照</el-button>
            <el-button v-show="isPhoto" type="primary" @click="gobackCamera()">重拍</el-button>
            <el-button v-show="isPhoto" type="primary" @click="upload()">上传</el-button>
        </div>
    </div>
</template>

<script>
import MyCameraSwf from '@/assets/MyCamera.swf';
import { uploadBase64 } from '@/api/member';

export default {
    props: {
        isShowCamera: {
            type: Boolean
        }
    },
    data() {
        return {
            base64Data: '',
            MyCameraSwf,
            stream: null,
            isPhoto: false,
            imgSrc: ''
        }
    },
    created() {
        this.polyfill();
        console.log(this.isShowCamera);
    },
    watch: {
        isShowCamera() {
            if (this.isShowCamera) {
                this.initCamera();
            } else if (!this.isShowCamera && this.stream) {
                this.stream.getVideoTracks()[0].stop();
                this.stream = null;
            }
        }
    },
    methods: {
        polyfill() {
            var promisifiedOldGUM = function(constraints) {

                // First get ahold of getUserMedia, if present
                var getUserMedia = (navigator.getUserMedia ||
                    navigator.webkitGetUserMedia ||
                    navigator.mozGetUserMedia);

                // Some browsers just don't implement it - return a rejected promise with an error
                // to keep a consistent interface
                if (!getUserMedia) {
                    return Promise.reject(new Error('getUserMedia is not implemented in this browser'));
                }

                // Otherwise, wrap the call to the old navigator.getUserMedia with a Promise
                return new Promise(function(resolve, reject) {
                    getUserMedia.call(navigator, constraints, resolve, reject);
                });

            }
            if (navigator.mediaDevices === undefined) {
                navigator.mediaDevices = {};
            }
            if (navigator.mediaDevices.getUserMedia === undefined) {
                navigator.mediaDevices.getUserMedia = promisifiedOldGUM;
            }
            console.log('polyfill');

        },
        /**
		 *  判断浏览器是否为IE或者其他浏览器
		 */
        doVerificationBrowser() {
            // 取得浏览器的userAgent字符串
            const userAgent = navigator.userAgent;
            // 判断浏览是否为IE
            if (!!window.ActiveXObject || "ActiveXObject" in window || userAgent.indexOf("trident") > -1) {
                return true;
            } else {
                return false;
            }
        },
        /**
		 * 检测浏览器是否兼容HTML5
		 */
        checkHtml5() {
            return !!document.createElement("video").canPlayType;
        },
        initCamera() {
            if (!this.doVerificationBrowser()) {
                this.doHtml5();
            }
        },
        /**
		 * 使用HTML5调用摄像头拍摄照片(适用于非IE浏览器)
		 */
        doHtml5() {
            const canvas = this.$refs.cameraCanvas;
            if (!canvas) {
                const _this = this;
                setTimeout(function() {
                    _this.doHtml5();
                }, 100);
                return;
            }
            clearTimeout();
            const context = canvas.getContext("2d");
            const video = this.$refs.cameraVideo;
            // const videoObj = { "video": true };
            const errBack = function(error) {
                console.log("Video capture error: ", error.code);
            };

            const _this = this;
            var constraints = { audio: false, video: true };
            //获得摄像头并显示到video区域  
            navigator.mediaDevices.getUserMedia(constraints)
                .then(function(stream) {
                    _this.stream = stream;
                    video.src = window.URL.createObjectURL(stream);
                    video.onloadedmetadata = function(e) {
                        video.play();
                    };
                })
                .catch(function(err) {
                    console.log(err.name + ": " + err.message);
                });
        },
        /**
		 * 拍摄照片(非IE)
		 */
        takePhotoForHtml5() {
            const canvas = this.$refs.cameraCanvas;
            const context = canvas.getContext("2d");
            const video = this.$refs.cameraVideo;
            //拍照画在画布上
            context.drawImage(video, 0, 0, 600, 450);
            //从画布上获取照片数据
            const imgData = canvas.toDataURL();
            //将图片转换为Base64
            this.base64Data = imgData.substr(22);
        },
        /**
		 * 使用FLASH调用摄像头拍照(适用于IE浏览器)
		 */
        takePhotoForFlash() {
            const MyCan = document["My_Cam"];
            this.base64Data = MyCan.GetBase64Code();
        },
        /**
         * 拍照
         */
        photo() {
            if (this.doVerificationBrowser()) {
                // IE
                this.takePhotoForFlash();
            } else {
                // 非IE
                this.takePhotoForHtml5();
            }
            this.isPhoto = true;
        },
        gobackCamera() {
            this.isPhoto = false;
        },
        upload() {
            uploadBase64({
                base64: this.base64Data
            }).then(response => {
                console.log('http://' + response.data.pic_address);
                this.imgSrc = 'http://' + response.data.pic_address;
                this.$emit('getCamera', this.imgSrc);
                this.isPhoto = false;
            })
        }
    },
    mounted() {
        this.initCamera();
    }
}
</script>

<style>
.camera {
    width: 100%;
    overflow: auto;
}
</style>



